:root {
  --typical-text-color: #2c2c2c;
  --full-black-color: #000;
  --grey-text-color: #505050;
  --dark-green: #7eb101;
  --hover-green: #bbf330;
  --noclean-white: #fefefe;
  --orange-def: #ffe550;
  --button-green: #bbf330;
  --button-hover-green: #b5ff00;
  --button-wh-bgc: #fff;
  --button-hov-border-dark:   --small-radius: 30px;
  --full-round: 50%;
  --card-gap: 32px;
  --card-numbers: 1;
  --animation-time: 250ms cubic-bezier(.4, 0, .2, 1);
}

