  padding: 0;
}

img {
  max-width: 100%;
  height: auto;
  object-fit: contain;
  object-position: center;
  display: block;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.button {
  color: var(--typical-text-color);
  text-align: center;
  letter-spacing: .06em;
  cursor: pointer;
  background-color: var(--button-green);
  transition: background-color var(--animation-time);
  border: none;
  border-radius: 30px;
  justify-content: center;
  align-items: center;
  margin-left: auto;
  margin-right: auto;
  padding: 6px 18px;
  font-family: inherit;
