.card-set {
  gap: var(--card-gap);
  flex-wrap: wrap;
  display: flex;
}

.card-item {
  flex-basis: calc((100% - (var(--card-numbers)  - 1) * var(--card-gap) ) / var(--card-numbers) );
}

.slider {
  border-radius: var(--full-round);
  transition: border-color var(--animation-time);
  grid-area: slider;
  position: relative;
}

@media (max-width: 767.98px) {
