.slider__vector {
  fill: var(--typical-text-color);
  flex-shrink: 0;
  display: flex;
  box-shadow: 0 4px 4px rgba(0, 0, 0, .06);
}

.slider:hover, .slider:focus {
  border-color: var(--hover-green);
}

.slider__vector--hover {
  opacity: 0;
  transition: opacity var(--animation-time);
}

.slider__control:hover ~ .slider__icon .slider__vector--hover, .slider__control:focus ~ .slider__icon .slider__vector--hover {
  opacity: 1;
}

.slider__control:active ~ .slider__icon .slider__vector {
  fill: var(--typical-text-color);
  box-shadow: 0 4px 4px rgba(0, 0, 0, .16);
}

